// Responsive
@include mq(lg) {
	// .shop-sidebar .sidebar-content {
	// 	padding-top: 3rem;
	// }
	.sidebar:not(.closed) .sticky-sidebar-fixed > .filter-actions {
		visibility: hidden;
	}
	.sidebar-fixed {
		.filter-actions:first-child {
			padding-top: 3rem;
		}

		// &.shop-sidebar .sidebar-content {
		// 	padding: 0 0 2rem;
		// }
		
		// 5. Toggle Fixed Sidebar
		transition: margin .4s;

		+ .main-content {
			transition: flex-basis .4s, max-width .4s;
		}
	}

	.sticky-sidebar-wrapper.closed {
		height: 0; // to solve issue occurs by closed sticky sidebar for fixed sidebar
	}
	.closed.sidebar {
		margin-left: -25%;
	}
	.closed.right-sidebar {
		margin-right: -25%;
	}

	.closed + .main-content {
		max-width: 100%;
		flex-basis: 100%;
	}

	// .main-content-wrap {
	// 	overflow: hidden;
	// }

	// Remain Toggle
	.sidebar-toggle-remain {
		& .toggle-remain {
			position: absolute;
			transition: .4s;
			left: 0;
			@include only-for-ie() {
				top: 2.7rem;
			}
		}
		.filter-clean {
			line-height: 3.7rem;
		}
		
		// left sidebar
		&.sidebar {
            .toggle-remain i {
                transition: transform .3s;
            }
			&.closed {
				.toggle-remain {
					left: calc(100% + 30px);

					i {
						transform: rotateY( 180deg );
					}
				}

				+ * > .toolbox:first-child {
                    margin-left: 10.8rem;
				}
			}

			// for only left sidebar to hide "clean all"'s movement
			.toggle-remain::before {
				content: '';
				position: absolute;
				background-color: #fff;
				right: calc(100% + 2px);
				width: 20px;
				bottom: -2px;
				top: -2px;
			}
		}

		// right sidebar
		&.right-sidebar.closed {
			.toggle-remain {
				left: -3rem;
				transform: translateX(-100%);

				i::before {
					content: "\e97e";
				}
			}

			+ * > .toolbox:first-child {
				padding-right: 11rem;
			}
		}
    }
    .toolbox-wrap .sidebar-content {
        border-top: 3px solid $border-color-light;
        transition: border .3s;
        
        & > div {
            border-bottom: 3px solid $border-color-light;
        }
        .widget {
            border: none;
        }
    }
    .shop-sidebar .filter-actions {
        margin-bottom: 0;
    }
}

@include mq(xs, max) {   
    .sticky-toolbox.fixed {
        padding: 1rem 1.5rem 0;
    }
    .toolbox {
        padding-right: 0;
        padding-left: 0;
    }
}

@include mq(sm, max) {
    .toolbox-item.select-box {
        margin-right: 0;   
    }

    .toolbox-item.select-box ~ .toolbox-item.right-sidebar-toggle {
        margin-left: 1rem;  
    }
    
    .toolbox-item.toolbox-layout {
        display: none;
    }

    .toolbox .toolbox-left .left-sidebar-toggle,
    .sticky-sidebar .filter-actions .sidebar-toggle-btn {
        margin-right: 1rem;
    }

    .toolbox  {
        .toolbox-left {
            margin: 0;
        }

        .left-sidebar-toggle {
            margin-right: 0.5rem;
        }

        .toolbox-sort .form-control {
            max-width: 12.8rem;
        }
    }

    .toolbox-horizontal {
        justify-content: flex-start;
        flex-direction: row;

        .toolbox-left {
            margin-right: auto;
        }

        .toolbox-right {
            margin-left: auto;
        }

        .toolbox-item.toolbox-show {
            margin-right: 0;   
        }
    }
    .toolbox-pagination {
        flex-direction: column;

        > .show-info {
            margin-right: 0;
        }

        > *:not(:last-child) {
            margin-bottom: 1rem;
        }
    }
}

@include mq(sm) {
    .toolbox-horizontal {
        .toolbox-left {
            margin-right: auto;
        }
    }
    // issue
    .mr-sm-auto {
        margin-right: auto!important;
    }
}

@include mq(md,max) {
    .toolbox-item {
        label, > span, &.show-info {
            display: none;
        }
    }
}

@include mq(lg, max) {
    // Shop Sidebar : Mobile
    .toolbox {
        .sidebar-fixed {
            margin-right: 0;
        }
        .sidebar-content {
            display: block;
        }
    }

    // Shop Sidebar's Select Menu : Mobile
    .sidebar-content {
        .select-menu {
            display: block;
            margin: 0;
            font-size: 1.4rem;

            ul {
                position: static;
                width: 100%;
                padding: 0;
                border: 0;
                color: #666;
                margin: 0 0 1.1rem;
                opacity: 1;
                visibility: visible;

                a::before {
                    width: 18px;
                    height: 18px;
                    margin-top: 0;
                }
            }

            &::before,
            ul::before,
            > a::before {
                content: none;
            }
        }
        .select-menu-toggle {
            display: block;
            border-width: 3px 0 0;
            padding: 2rem 2px 1.6rem;
            font-size: 1.5rem;
            font-weight: 600;
            text-transform: capitalize;
        }
    }

    .toolbox-item:first-child a {
        border: none;
    }

    // Navigation Style
    // .toolbox-wrap {
    //     .widget-title {
    //         border-top: 3px solid $border-color-light;
    //     }
    // }

    .card.navigation-card.collapsed .card-header + div {
        display: block !important;
    }

    .card.navigation-card.expanded + .toolbox .toolbox-right {
        opacity: 1 !important;
    }
}

@include mq(lg) {
    .toolbox .sidebar-content {
        max-width: none;
    }

    .toolbox-horizontal {
        .sidebar-fixed {
            .sidebar-content {
                padding-bottom: 0;
            }
        }
    }

    // Navigation Style
    .toolbox-wrap {
        // .sidebar-content {
        //     border: solid #ebebeb;
        //     border-width: 3px 0;
        //     margin-bottom: 2rem;
        //     transition: border-color .3s;
        // }

        .filter-actions:first-child {
            padding: 0;
        }

        .sidebar-toggle-btn {
            display: none;
        }

        .sidebar.closed {
            margin: 0;

            .sidebar-content {
                display: none;

                // issue for toggle animation
                border-color: transparent;
                background: transparent;
            }
        }
        .filter-clean {
            display: none;
            position: absolute;
            right: 0;
            top: -40px;
        }
    }
}
@include mq(xl) {
    .select-menu:not(:last-child):not(.toolbox-show) {
        margin-right: 2rem;
    }
    .toolbox .shop-sidebar { margin-right: 2rem }
}

.owl-carousel {
    display: none;
    width: 100%;
    -webkit-tap-highlight-color: transparent;
    position: relative;
    z-index: 1
}

.owl-carousel .owl-stage {
    position: relative;
    -ms-touch-action: pan-Y;
    touch-action: manipulation;
    -moz-backface-visibility: hidden
}

.owl-carousel .owl-stage:after {
    content: ".";
    display: block;
    clear: both;
    visibility: hidden;
    line-height: 0;
    height: 0
}

.owl-carousel .owl-stage-outer {
    position: relative;
    overflow: hidden;
    -webkit-transform: translate3d(0,0,0)
}

.owl-carousel .owl-item,.owl-carousel .owl-wrapper {
    -webkit-backface-visibility: hidden;
    -moz-backface-visibility: hidden;
    -ms-backface-visibility: hidden;
    -webkit-transform: translate3d(0,0,0);
    -moz-transform: translate3d(0,0,0);
    -ms-transform: translate3d(0,0,0)
}

.owl-carousel .owl-item {
    position: relative;
    min-height: 1px;
    float: left;
    -webkit-backface-visibility: hidden;
    -webkit-tap-highlight-color: transparent;
    -webkit-touch-callout: none
}

.owl-carousel .owl-item img {
    display: block;
    width: 100%
}

.owl-carousel .owl-dots.disabled,.owl-carousel .owl-nav.disabled {
    display: none
}

.owl-carousel .owl-dot,.owl-carousel .owl-nav .owl-next,.owl-carousel .owl-nav .owl-prev {
    cursor: pointer;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none
}

.owl-carousel .owl-nav [class*=owl-],.owl-carousel button.owl-dot {
    background: 0 0;
    color: inherit;
    border: none;
    padding: 0!important;
    font: inherit
}

.owl-carousel.owl-loaded {
    display: block
}

.owl-carousel.owl-loading {
    opacity: 0;
    display: block
}

.owl-carousel.owl-hidden {
    opacity: 0
}

.owl-carousel.owl-refresh .owl-item {
    visibility: hidden
}

.owl-carousel.owl-drag .owl-item {
    touch-action: pan-y;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none
}

.owl-carousel.owl-grab {
    cursor: move;
    cursor: grab
}

.owl-carousel.owl-rtl {
    direction: rtl
}

.owl-carousel.owl-rtl .owl-item {
    float: right
}

.no-js .owl-carousel {
    display: block
}

.owl-carousel .animated {
    animation-duration: 1s;
    animation-fill-mode: both
}

.owl-carousel .owl-animated-in {
    z-index: 0
}

.owl-carousel .owl-animated-out {
    z-index: 1
}

.owl-carousel .fadeOut {
    animation-name: fadeOut
}

@keyframes fadeOut {
    0% {
        opacity: 1
    }

    100% {
        opacity: 0
    }
}

.owl-height {
    transition: height .5s ease-in-out
}

.owl-carousel .owl-item .owl-lazy {
    opacity: 0;
    transition: opacity .4s ease
}

.owl-carousel .owl-item .owl-lazy:not([src]),.owl-carousel .owl-item .owl-lazy[src^=""] {
    max-height: 0
}

.owl-carousel .owl-item img.owl-lazy {
    transform-style: preserve-3d
}

.owl-carousel .owl-video-wrapper {
    position: relative;
    height: 100%;
    background: #000
}

.owl-carousel .owl-video-play-icon {
    position: absolute;
    height: 80px;
    width: 80px;
    left: 50%;
    top: 50%;
    margin-left: -40px;
    margin-top: -40px;
    // background: url(owl.video.play.png) no-repeat;
    cursor: pointer;
    z-index: 1;
    -webkit-backface-visibility: hidden;
    transition: transform .1s ease
}

.owl-carousel .owl-video-play-icon:hover {
    transform: scale(1.3,1.3)
}

.owl-carousel .owl-video-playing .owl-video-play-icon,.owl-carousel .owl-video-playing .owl-video-tn {
    display: none
}

.owl-carousel .owl-video-tn {
    opacity: 0;
    height: 100%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    transition: opacity .4s ease
}

.owl-carousel .owl-video-frame {
    position: relative;
    z-index: 1;
    height: 100%;
    width: 100%
}

.owl-theme .owl-nav {
    margin-top: 0;
    text-align: center;
    -webkit-tap-highlight-color: transparent
}

.owl-theme .owl-nav [class*=owl-] {
    position: absolute;
    top: 50%;
    z-index: 99;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 30px;
    height: 30px;
    margin: 0;
    margin-top: -15px;
    color: #2b579a;
    font-size: 16px;
    line-height: 1;
    padding: 0;
    border: .1rem solid #ebebeb;
    background: 0 0;
    cursor: pointer;
    border-radius: 50px;
    transition: all .3s ease
}

.owl-theme .owl-nav [class*=owl-]:not(.disabled):hover {
    color: #fff;
    border-color: #2b579a;
    background: #2b579a;
    text-decoration: none
}

.owl-theme .owl-nav .disabled {
    opacity: .8;
    cursor: default
}

.owl-theme .owl-nav .owl-prev {
    left: -50px
}

.owl-theme .owl-nav .owl-next {
    right: -50px
}

.owl-theme .owl-nav.disabled+.owl-dots {
    margin-top: 10px
}

.owl-theme .owl-dots {
