}

.card-header {
    text-transform: capitalize;
    font-size: 1.8rem;
    font-weight: 600;
    line-height: 1;
    color: #222
}

.card-header a {
    display: -moz-box;
    display: flex;
    -moz-box-align: center;
    align-items: center;
    position: relative;
    padding: 1.8rem .5rem
}

.card-header a:hover,.card-header a:hover:after {
    color: #222
}

.card-header a:after {
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    transform: translateY(-50%);
    right: .5rem;
    font-family: riode;
    font-size: 1.8rem;
    font-weight: 400;
    letter-spacing: -.01em;
    color: #222
}

