    -moz-transform: translateY(-50%);
    transform: translateY(-50%);
    font-family: riode;
    font-size: 1.6rem;
    font-weight: 400;
    opacity: 0;
    -webkit-transition: opacity .3s;
    -moz-transition: opacity .3s;
    transition: opacity .3s
