    font-size: 3rem
}

.faq .page-title {
    margin-bottom: .2rem;
    font-size: 5rem
}

.faq section {
    padding-top: 4.1rem
}

.faq .card {
    border-width: 1px 0 0
}

.faq .card,.faq .card:last-child {
    border-color: #ebebeb
}

.faq .card-header {
    font-size: 1.6rem;
    text-transform: none;
    letter-spacing: -.025em
}

.faq .card-header a {
    padding: 1.7rem 1rem
}

.faq .card-header a:after {
    font-size: 1.5rem;
    right: 1.3rem;
    color: #999
}

.faq .card-body {
    padding: .2rem 1rem
}

.cart-dropdown.type2 .cart-count {
    background-color: #ff675d
}

.intro-slider img {
    min-height: 55rem;
    object-fit: cover
}

@media(min-width: 576px) {
    .d-sm-flex {
        display:-moz-box!important;
        display: flex!important
    }

    .mr-sm-4 {
        margin-right: 2rem!important
    }
}

@media(min-width: 768px) {
    .d-md-block {
        display:block!important
    }

    .mr-md-0 {
        margin-right: 0!important
    }
}

@media(max-width: 991px) {
    .intro-slider img {
        min-height:50rem
    }
}

@media(max-width: 767px) {
    .intro-slider img {
        min-height:45rem
    }
}

.intro-slide1 img {
    object-position: 20% center
}

.intro-slide1 .banner-content {
    max-width: 64.5rem
}

.intro-slide1 .banner-subtitle {
    font-size: 3em
}

.intro-slide1 .banner-title {
    margin-bottom: .1rem;
    font-weight: 800;
    font-size: 8em;
    line-height: 1.1;
    letter-spacing: .2em
}

.intro-slide1 h5 {
    font-size: 3em
}

.intro-slide2 .banner-content {
    left: 8.3%
}

.intro-slide2 .banner-subtitle {
    margin-bottom: .2rem;
    font-size: 3em;
    color: hsla(0,0%,100%,.6)
}

.intro-slide2 .banner-title {
    margin-bottom: 1.7rem;
    font-size: 5em
}

.intro-slide2 h5 {
    font-size: 3em
}

.intro-slide2 p {
    font-size: 1.4em;
    color: hsla(0,0%,100%,.6);
    line-height: 1.5
}

.intro-slide2 .btn {
    font-size: 1.4em
}

.intro-banner img {
    min-height: 18rem;
    object-fit: cover
}

.intro-banner .banner-content {
    margin-top: .2rem
}

.intro-banner .banner-subtitle {
    margin-bottom: .7rem;
    font-size: 1.8rem
}

.intro-banner .banner-title {
    margin-bottom: 2.4rem;
    font-size: 3em;
    line-height: .95
}

.intro-banner .btn i {
    margin-left: .9rem
}

.intro-banner1 .banner-content {
    right: 6%
}

.intro-banner2 .banner-content {
    right: 8%
}

.category-section .title {
    margin-bottom: 2.7rem
}

.category-section .banner-content {
    padding: 3rem 0 2rem
}

.category-section .banner-title {
    font-size: 2.4rem;
    line-height: 1.5em
}

.category-section .banner-subtitle {
    font-size: 2.4rem
}

.category-section .btn-group a {
    text-transform: capitalize;
    opacity: .9
}

.category-section .btn-group a:first-child {
    margin-right: .7rem
}

.category-section .category-content {
    bottom: 2rem
}

.height-x1 {
    height: 30rem
}

.height-x2 {
    height: 60rem
}

.banner-sale {
    padding: 6.8rem 5.8rem 6.2rem
}

.banner-sale .banner-subtitle {
    font-size: 2.6rem
}

.banner-sale .divider {
    width: 19rem;
    height: .2rem;
    background-color: #fff;
    opacity: .2
}

.banner-sale .banner-title {
    font-size: 7.6rem;
    font-weight: 800;
    line-height: 1;
    letter-spacing: .05em
}

.banner-sale .banner-price-info {
    position: absolute;
    left: 23.7%;
    top: -10%;
    border: 3px solid hsla(0,0%,100%,.6);
    border-radius: 50%;
    width: 14rem;
    height: 14rem;
    -webkit-transform: rotate(-15deg);
    -moz-transform: rotate(-15deg);
    transform: rotate(-15deg);
    text-indent: 18px
}

.banner-sale .banner-price-info h5 {
    font-size: 1.8em;
    font-weight: 500;
    line-height: 1.3
}

.banner-sale .banner-price-info span {
    font-size: 1.944em;
    font-weight: 800
}

.home .service-list {
    border-top: 1px solid #eee;
    border-bottom: 1px solid #eee
}

.home .service-list .icon-box-content {
    padding: 2.4rem 0
}

.home .service-list .icon-box-title {
    font-size: 1.6rem
}

.home .service-list p {
    line-height: 1.6
}

.widget.widget-about {
    margin-top: -.8rem
}

@media(max-width: 1024px) {
    .banner-sale {
        background-position:0
    }

    .banner-sale .banner-price-info {
        left: 36%
    }
}

@media(max-width: 991px) {
    .footer-middle {
        padding:7.6rem 0 4.6rem
    }
}

@media(max-width: 767px) {
    .intro-slide1,.intro-slide2 {
        font-size:.7rem
    }

    .height-x1 {
        height: 22.5rem
    }

    .height-x2 {
        height: 45rem
    }

    .banner-sale {
        padding: 3rem 2rem 17rem;
        background-position: 50%
    }

    .banner-sale .banner-price-info {
        left: 0;
        top: 100%
    }
}

@media(max-width: 575px) {
    .height-x1 {
        height:45rem
    }

    .height-x1>.category {
        height: 50%
    }

    .intro-slide1 {
        font-size: .5rem
    }

    .intro-slide1 .banner-title {
        letter-spacing: 0
    }

    .intro-slide2 {
        font-size: .65rem
    }
}

@media(max-width: 479px) {
    .intro-banner {
        font-size:.8rem
    }

    .intro-slide1 .banner-content {
        left: 1.5rem
    }
}

.breadcrumb {
    line-height: 1.9
}

.shop-banner {
    padding: 5.2rem 0 5.1rem
}

.shop-banner .banner-content {
    right: 8.5%
}

.shop-banner .banner-title {
    margin-bottom: .4rem
}

.shop-banner .banner-price-info {
    margin-bottom: 1.6rem
}

.shop-banner .banner-price-info,.shop-banner .banner-subtitle {
    font-size: 2em
}

.shop-banner .banner-title {
    font-size: 6em
}

.shop-banner p {
    margin-bottom: 2.3rem;
    padding: 0 1.1rem;
    line-height: 3rem
}

@media(max-width: 479px) {
    .shop-banner .banner-content {
        right:1.5rem
    }
}

.product-details .product-navigation {
    padding: 0 2px 1.2rem
}

.mobile-menu li>a:hover {
    color: #ff675d
}
